import React from 'react'

import NavBar from './NavBar'
import JohnVoong from '../assets/JohnVoong.jpg'


export default function About() {
  return (
    <>
      <NavBar />
      <div className="about-page">
        <h1>About me</h1>
        <p>Hi! I'm John, an aspiring software engineer who enjoys web development on his free time!<br />
          Currently taking part of the Wonsulting 2020 Project
        </p>
        <img className="john-voong" alt={"John Voong"} src={JohnVoong} />
      </div>
    </>
  )
}